@import '../../styles/theme.scss';
@import '../../styles/breakpoints.scss';

.header {
  background: $accent-blue;
  color: $general-white;

  & .container {
    padding: 3rem 5rem;
    max-width: unset;
  }

  &__title {
    transition: all 0.3s ease;
    font-size: 4rem;
    background: $general-white;
    color: $text-main;
    border: 1px solid;
    padding: 1rem;
    border-radius: 0.8rem;

    &:hover {
      transform: skew(-15deg);
    }
  }

  &__button {
    font-size: 5rem;
    color: $general-white;
    transition: all 0.3s ease;

    &:hover {
      color: darken($color: $general-white, $amount: 20);
    }
  }
}

@media only screen and (max-width: $breakpoint-tablet) {
  .header {
    & .container {
      padding: 2rem 4rem;
    }

    &__title {
      font-size: 3.2rem;
    }

    &__button {
      font-size: 4rem;
    }
  }
}

@media only screen and (max-width: $breakpoint-mobile) {
  .header {
    & .container {
      padding: 1rem 2rem;
    }

    &__title {
      font-size: 2rem;
    }

    &__button {
      font-size: 3rem;
    }
  }
}
