@import '../../styles/theme.scss';
@import '../../styles/breakpoints.scss';

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: $general-white;
  font-size: 5rem;
  color: $accent-blue;

  & .container {
    padding-top: 0;
    padding-bottom: 0;
  }

  &__menu {
    border: 0.2rem solid $text-main;
    padding: 2rem 5rem;
    border-bottom: 0;
    margin: 0 auto;
    border-radius: 0.8rem;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;

    &-item {
      transition: all 0.3s ease;

      &:hover {
        color: darken($color: $accent-blue, $amount: 10);
      }
    }
  }
}

@media only screen and (max-width: $breakpoint-tablet) {
  .footer {
    font-size: 4rem;

    &__menu {
      padding: 2rem 2rem;
    }
  }
}

@media only screen and (max-width: $breakpoint-tablet) {
  .footer {
    font-size: 3rem;

    &__menu {
      padding: 1rem 1rem;
    }
  }
}
