@import '../../styles/theme.scss';
@import '../../styles/breakpoints.scss';

.card {
  margin-bottom: 5rem;
  border: 0.1rem solid;
  padding: 5rem;
  width: calc(50% - 3rem);

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 65rem;
  }

  &__image-wrapper {
    height: 40rem;
  }

  &__title {
    text-align: center;
  }

  &__tag {
    text-align: center;
  }

  &__input {
    text-align: center;
    padding: 2rem;

    &-wrapper {
      margin: 0 auto;

      border: 0.1rem solid $text-main;
      border-radius: 0.8rem;
    }
  }

  &__button {
    margin: 0 auto;
  }
}

@media only screen and (min-width: $breakpoint-tablet) and (max-width: $breakpoint-pc) {
  .card {
    margin-bottom: 3.2rem;
    padding: 3rem;
    width: calc(50% - 2.4rem);

    &__wrapper {
      height: 50rem;
    }

    &__image-wrapper {
      height: 25rem;
    }

    &__input {
      padding: 1.5rem;
    }
  }
}

@media only screen and (min-width: $breakpoint-mobile) and (max-width: $breakpoint-tablet) {
  .card {
    margin-bottom: 2rem;
    padding: 3rem;
    width: calc(50% - 1.6rem);

    &__wrapper {
      height: 30rem;
    }

    &__image-wrapper {
      height: 13rem;
    }

    &__input {
      padding: 1rem;
    }
  }
}
