@import '../../styles/theme.scss';

.search {
  background: $general-white;

  &__input {
    &-wrapper {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        top: 120%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 0.2rem;
        background: $text-main;
      }
    }
  }
}
