@import '../../styles/breakpoints.scss';

.bookmarks {
  &__title {
    text-align: center;
    font-weight: 700;
    margin-bottom: 5rem;
  }
}

@media only screen and (max-width: $breakpoint-tablet) {
  .bookmarks {
    &__title {
      margin-bottom: 3rem;
    }
  }
}

@media only screen and (max-width: $breakpoint-mobile) {
  .bookmarks {
    &__title {
      margin-bottom: 2rem;
    }
  }
}
