@import './styles/theme.scss';
@import './styles/breakpoints.scss';

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local(''), url('./fonts/roboto-regular.woff2') format('woff2'),
    url('./fonts/roboto-regular.woff') format('woff');
}

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local(''), url('./fonts/roboto-bold.woff2') format('woff2'),
    url('./fonts/roboto-bold.woff') format('woff');
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 2.4rem;
  color: $text-main;
  min-width: 425px;
}

button {
  cursor: pointer;
  color: inherit;
  font-size: inherit;
  background: transparent;
}

input {
  color: inherit;
  font-size: inherit;
  background: transparent;
  outline: 0;
  width: 100%;
}

a {
  color: inherit;
  font-size: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
}

.container {
  max-width: 1232px;
  margin: 0 auto;
  width: 100%;
  padding: 12rem 1.6rem;
}

.row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  border: 1px solid $text-main;
  transition: all 0.3s ease;

  &:hover {
    background: $text-main;
    color: $general-white;
  }

  &--disabled {
    opacity: 0.5;
    cursor: default;

    &:hover {
      background: unset;
      color: unset;
    }
  }
}

@media only screen and (max-width: $breakpoint-tablet) {
  body {
    font-size: 1.6rem;
  }

  .container {
    max-width: 712px;
    padding: 8rem 1.6rem;
  }

  .button {
    padding: 1.5rem;
  }
}

@media only screen and (max-width: $breakpoint-mobile) {
  body {
    font-size: 1.2rem;
  }

  .container {
    max-width: 410px;
    padding: 6rem 1.6rem;
  }

  .button {
    padding: 1rem;
  }
}
